@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  font-family: Arial;
  background-image: linear-gradient(
    to right top,
    #4d0155,
    #8e0055,
    #c12948,
    #e16031,
    #eb9a12
  );
  background-attachment: fixed;
  text-align: center;
  color: white;
}

button {
  font-family: inherit;
  padding: 1rem;
  margin: 1rem;
  background: white;
  box-shadow: 0 0 5px grey;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  text-transform: uppercase;
}
.App {
  text-align: center;
}

.circle {
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background-color: white;
    display: inline-block;
    margin: 1rem;
}

.green.active {
    background-color: green;
}

.blue.active {
    background-color: blue;
}

.red.active {
    background-color: red;
}

.yellow.active {
    background-color: yellow;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: grid;
    justify-items: center;
    align-items: center;
}

.gameover-box {
    position: fixed;
    top: 25%;
    left: 25%;
    height: 200px;
    width: 50%;
    background: white;
    display: grid;
    justify-content: center;
    align-items: center;
    color: black;
}

.gameover-box button {
    background: lightgrey;
}
